import React from 'react';
import PropTypes from 'prop-types';

MovieList.propTypes = {
  movies: PropTypes.array.isRequired,
};

function MovieList({ movies }) {
  return (
    <div>
      {movies && movies.map(movie => <MovieItem movie={movie} />)}
    </div>
  );
}

MovieItem.propTypes = {
  movie: PropTypes.object.isRequired,
};
function MovieItem({ movie }) {
  const { id, title, year,  summary, genres, rating, medium_cover_image  } = movie;
  return (
    <div key={id} className="movie">
      <img src={medium_cover_image} alt={title} title={title} />
      <div className="movie__data">
        <h3 className="movie__title">{title}</h3>
        <h5>{rating}</h5>
        <h5 className="movie__year">{year}년 작품</h5>
        <ul className="movie__genres">{genres.map((genre, index) => <li key={index}>{genre}</li>)}</ul>
        <p className="movie__summary">{summary}</p>
      </div>

    </div>
  );
}

export default MovieList;
